import Link from "next/link";

export default function ReportDetailPage({
  params,
}: {
  params: { id: string };
}) {
  return (
    <main className="max-w-3xl mx-auto py-8 space-y-6">
      {/* 上部ヘッダー */}
      <div className="flex items-center justify-between gap-4">
        <div>
          <Link href="/reports" className="text-xs underline text-slate-600">
            ← 週報一覧に戻る
          </Link>
          <h1 className="mt-2 text-2xl font-bold">
            週報ID: {params.id} の詳細
          </h1>
          <p className="mt-1 text-sm text-slate-600">
            ここに「{params.id}」番目の週報の内容を表示・編集していく予定です。
          </p>
        </div>
      </div>

      {/* 今週やったこと */}
      <section className="space-y-2">
        <h2 className="text-lg font-semibold">今週やったこと</h2>
        <p className="text-sm text-slate-600">
          ここに、この週に完了したタスクや取り組んだことのまとめが入ります。
          後でテキストエリアにして編集できるようにします。
        </p>
      </section>

      {/* 来週やること */}
      <section className="space-y-2">
        <h2 className="text-lg font-semibold">来週やること</h2>
        <p className="text-sm text-slate-600">
          来週に予定しているタスクや目標を書くエリアです。
        </p>
      </section>

      {/* 課題・相談事項 */}
      <section className="space-y-2">
        <h2 className="text-lg font-semibold">課題・相談したいこと</h2>
        <p className="text-sm text-slate-600">
          詰まっていることや、上長・メンバーに相談したいことをメモしておく場所です。
        </p>
      </section>
    </main>
  );
}
